import axios from 'axios'
import moment from 'moment'
import { types } from '../types/types'

export const getAllProductCategories = () => {
  return (dispatch) => {
    axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/product_categories`)
      .then((response) => {
        console.log(response)

        dispatch(getProductCategories(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addProductCategoryAPI = (productCategory) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/product_categories`, productCategory)
      .then((response) => {
        dispatch(createProductCategory(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateProductCategoryAPI = (productCategory, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/product_categories/${id}`, productCategory)
      .then((response) => {
        dispatch(updateProductCategory(productCategory))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const createProductCategory = (productCategory) => ({
  type: types.createProductCategory,
  payload: productCategory,
})

export const updateProductCategory = (productCategory) => ({
  type: types.updateProductCategory,
  payload: productCategory,
})

export const getProductCategories = (productCategories) => ({
  type: types.getProductCategories,
  payload: productCategories,
})
